@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300&display=swap);
#root{color:#fff;font-family:"Poiret One",cursive}#root a{text-decoration:none;color:#fff}.bg-main{min-height:100vh;background-color:#000}.cont-main{min-height:100vh;padding-top:40px;display:flex;flex-direction:column;justify-content:space-between}@media screen and (max-width: 992px){.cont-main{padding-top:30px}}@media screen and (max-width: 768px){.cont-main{padding-top:20px}}

form{margin-top:40px}form label{font-size:24px;margin:25px 0}form textarea{font-family:'Lato', sans-serif;font-size:19px !important;color:black !important;background-color:rgba(255,255,255,0.8) !important;border:none !important}form textarea::-webkit-input-placeholder{font-size:19px;color:#3E3E3C !important}form textarea:-ms-input-placeholder{font-size:19px;color:#3E3E3C !important}form textarea::-ms-input-placeholder{font-size:19px;color:#3E3E3C !important}form textarea::placeholder{font-size:19px;color:#3E3E3C !important}form span{display:flex;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-top:20px}form a{font-family:'Lato', sans-serif;font-size:20px !important}

.header{border-bottom:2px solid white;padding-bottom:15px}.header img{border-radius:100%;margin-right:7px}.header img:hover{background-color:#7E7E6F;transition:.3s}.header nav{justify-content:space-between;align-items:center}.header nav h2{font-size:32px}.header nav h2 a{padding:5px}.about-us{margin-right:30px}.active-nav{border:1px solid white}.svg-logo{fill:red}@media screen and (max-width: 992px){.header h2{font-size:28px !important}}@media screen and (max-width: 576px){.header nav div{flex-direction:column}.header nav .about-us{padding-bottom:8px;border-bottom:1px solid white}}

.footer{display:flex;flex-direction:column;align-items:center;padding-bottom:50px}.bottom-line{height:2px;width:100%;background-color:#fff;margin-top:-30px}

.result-container{font-size:38px}

